import{GET_SUPERMARKETS, ADD_SUPERMARKET, DELETE_SUPERMARKET,ADD_ITEMTOSHOP,ADD_ITEMTOBASKET,DELETE_ALLFROMBASKET,
	DELETE_ITEMFROMBASKET,CHANGE_MARKERSELECTED,ITEMS_LOADING} from "./types"
import axios from "axios"

export const getSuperMarkets = () =>dispatch =>{
	dispatch(setItemsLoading())
	axios
		.get("api/superMarkets")
		.then(res=>dispatch({
			type:GET_SUPERMARKETS,
			payload: res.data
		}))
}
export const addSuperMarket = (superMarket)=>dispatch =>{
		axios
		.post("api/superMarkets",superMarket)
		.then((res)=>dispatch({
			type:ADD_SUPERMARKET,
			payload: res.data
		}))
}
export const deleteSuperMarket = (superMarket) =>dispatch =>{
	axios
		.delete(`api/superMarkets/${superMarket._id}`)
		.then((res)=>dispatch({
			type:DELETE_SUPERMARKET,
			payload: superMarket
		}))
}
export const addItemToShop = (item) =>{
	return {
		type:ADD_ITEMTOSHOP,
		payload: item
	}
}
export const addItemToBasket = (item) =>{
	return {
		type:ADD_ITEMTOBASKET,
		payload: item
	}
}
export const deleteItemFromBasket = (item) =>{
	return {
		type:DELETE_ITEMFROMBASKET,
		payload: item
	}
}
export const deleteAllBasket = (basket) =>{
	return {
		type:DELETE_ALLFROMBASKET,
	}
}
export const changeMarkerSelected = (supermarket)=>{
	return{
		type:CHANGE_MARKERSELECTED,
		payload:supermarket
	}
}
export const setItemsLoading = ()=>{
	return{
		type:ITEMS_LOADING
	}
}